@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;

}

body{
    font: 400 14px Roboto, sans-serif;
    background-color: #7159c1;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea, select{
    font: 400 18px Roboto, sans-serif
}

button{
    cursor: pointer;
}

form input{
    width: 100%;
    height: 50px;
    color: #333333;
    border: 1px solid ;
    border-radius: 8px;
    padding: 0 24px;
    margin-top: 10px;
}

.button {
    width: 100%;
    height: 60px;
    background: #7159C1;
    border: 0;
    border-radius: 8px;
    color: #E5E5E5;
    font-weight: 700;
    margin-top: 20px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}
.login-container{
    width: 100%;
    max-width: 700px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}  

.logo{
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    color: #E5E5E5;
}
.logo h1 {
    font-weight: 400;
}
.logo span{
    font-weight: bold;
}
.logo svg{
    position: absolute;
    top: 50%;
    left: 50%;
}

.formulario{
    width: 100%;
    background-color: #4C3E7D;
