/* The navigation bar */
.navbar {
    overflow: hidden;
    background-color: #4C3E7D;
    position: fixed; 
    top: 0;
    width: 100%; 
    height: 56px;

    display: flex;
    color: white;
}

.brand{
    display: flex;
    margin-left: 20px;
